import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-types-config',
  templateUrl: './types-config.component.html',
  styleUrls: ['./types-config.component.scss']
})
export class TypesConfigComponent implements OnInit {

  public title: string;
  public name: FormControl;
  public types: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public paginador: any;
  public mdRef: NgbModalRef;
  private isCreate: boolean;
  private categoriaSelect: any;

  constructor(private ngbModal: NgbModal, private catalogoService: CatalogoService, private router: Router) { }

  ngOnInit(): void {
    this.categoriaSelect = null;
    this.isCreate = false;
    this.types = [];
    this.title = "";
    this.name = new FormControl("", [Validators.required, Validators.maxLength(20)]);
    this.getTypesByNameOrAll("all", 1);
  }

  public clickSave(): void {
    if(this.isCreate) {
      this.createType();
    } else {
      this.updateType();
    }
  }

  private updateType(): void {
    LoaderComponent.status = true;
    this.catalogoService.updateCategoria(this.name.value, this.categoriaSelect.id).subscribe(
      response => {
        swal.fire("¡Tipo actualizado!", "", "success");
        this.mdRef.close();
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private createType(): void {
    LoaderComponent.status = true;
    this.catalogoService.createType(this.name.value).subscribe(
      response => {
        swal.fire("¡Tipo creado!", "", "success");
        this.mdRef.close();
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public changeName(): void {
    if(this.name.value == "") {
      this.getTypesByNameOrAll("all", 1);
    } else {
      this.getTypesByNameOrAll(this.name.value, 1);
    }
  }

  public changePage(page: number): void {
    if(this.name.value != "") {
      this.getTypesByNameOrAll(this.name.value, page);
    } else {
      this.getTypesByNameOrAll("all", page);
    }
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public getTypesByNameOrAll(name: string, page: number): void {
    LoaderComponent.status = true;
    this.catalogoService.findTypesByName(name, page, 10).subscribe(
      response => {
        this.types = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openFormMD(content: any, existencia: any): void {
    this.name.reset();
    if (existencia) {
      this.categoriaSelect = existencia;
      this.isCreate = false;
      this.title = "Actualizar tipo: " + existencia.name;
      this.name.setValue(existencia.name);
    } else {
      this.isCreate = true;
      this.title = "Crear tipo";
    }
    this.mdRef = this.ngbModal.open(content, { size: "lg" });
  }

}
