import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AdminComponent } from './admin.component';
import { InventarioComponent } from './inventario/inventario.component';
import { Routes, RouterModule } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { VentasComponent } from './ventas/ventas.component';
import { ReactiveFormsModule } from '@angular/forms';
import { PreparacionComponent } from './preparacion/preparacion.component';
import { ProductFormComponent } from './inventario/product-form/product-form.component';
import { ExistenciasComponent } from './inventario/existencias/existencias.component';
import { ExistenciasCreateComponent } from './inventario/product-form/existencias-create/existencias-create.component';
import { ConfigurationComponent } from './configuration/configuration.component';
import { InventarioConfigComponent } from './configuration/inventario-config/inventario-config.component';
import { TallaConfigComponent } from './configuration/inventario-config/talla-config/talla-config.component';
import { TypesConfigComponent } from './configuration/inventario-config/types-config/types-config.component';
import { UsersComponent } from './users/users.component';
import { FormUserComponent } from './users/form-user/form-user.component';
import { CotizacionesComponent } from './cotizaciones/cotizaciones.component';
import { SalesOfDayComponent } from './sales-of-day/sales-of-day.component';

const routes: Routes = [
  {
    path: "inventario", component: InventarioComponent
  },
  {
    path: "ventas", component: VentasComponent
  },
  {
    path: "preparacion/:pedidoId/:ventaId", component: PreparacionComponent
  },
  {
    path: "productForm", component: ProductFormComponent
  },
  {
    path: "configuration", component: ConfigurationComponent
  },
  {
    path: "users", component: UsersComponent
  },
  {
    path: "cotizaciones", component: CotizacionesComponent
  },
  {
    path: "salesOfDay", component: SalesOfDayComponent
  }
];


@NgModule({
  declarations: [AdminComponent, InventarioComponent, VentasComponent, PreparacionComponent, ProductFormComponent, ExistenciasComponent, ExistenciasCreateComponent, ConfigurationComponent, InventarioConfigComponent, TallaConfigComponent, TypesConfigComponent, UsersComponent, FormUserComponent, CotizacionesComponent, SalesOfDayComponent],
  imports: [
    NgbModule,
    CommonModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule
  ]
})
export class AdminModule { }
