import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { swalProviderToken } from '@sweetalert2/ngx-sweetalert2/lib/di';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { VentasService } from '../../../../services/ventas.service';
import swal from 'sweetalert2';
import { relativeTimeThreshold } from 'moment';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-ventas',
  templateUrl: './ventas.component.html',
  styleUrls: ['./ventas.component.scss']
})
export class VentasComponent implements OnInit {

  public ventasAmountByEstados: any[];
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public ventas: any[];
  public clienteDataMD: any;
  public direccionDataMD: any;
  public productosDataMD: any;
  public ventaEstados: string[];
  public isFiltro: boolean;
  public paginador: any;
  public estado: string;
  public size: number;
  public ventasBigFilter: boolean;
  public reciboUrl: string;
  public formFilters: FormGroup;
  public fechaEstimada: FormControl;
  public ventaSelect: {
    ventaId: number,
    pedidoId: number
  };
  private fechaEstimadaMD: NgbModalRef;

  constructor(private ventasService: VentasService, private router: Router, private ngModal: NgbModal) { }

  ngOnInit(): void {
    this.constructFormFilters();
    this.ventaSelect = null;
    this.reciboUrl = "";
    this.ventasBigFilter = false;
    this.size = 10;
    this.estado = "";
    this.isFiltro = false;
    this.findAllStatesVenta();
    this.ventas = [];
    LoaderComponent.status = true;
    this.ventasService.getVentasAmountByEstados().subscribe(
      response => {
        this.ventasAmountByEstados = response;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
    this.getVentasByEstado("all", 1);
  }

  private findAllStatesVenta() {
    LoaderComponent.status = true;
    this.ventasService.findAllStatesVenta().subscribe(
      response => {
        this.ventaEstados = response;
        this.ventaEstados.forEach(state => {
          state.replace("_", " ");
        });
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public openFinalizarMD(content: any, ventaId: number, pedidoId: number): void {
    this.ventaSelect = {
      ventaId: ventaId,
      pedidoId: pedidoId
    };
    this.fechaEstimada = new FormControl("", [Validators.required]);
    this.fechaEstimadaMD = this.ngModal.open(content, { size: "md" });
  }

  private constructFormFilters(): void {
    this.formFilters = new FormGroup({
      fechaInicio: new FormControl(null, [Validators.required]),
      fechaFin: new FormControl(null, [Validators.required]),
      apodo: new FormControl("", [Validators.required, Validators.maxLength(100)])
    });
  }

  public openRecibo(content: any, reciboUrl: string): void {
    this.reciboUrl = reciboUrl;
    this.ngModal.open(content, { size: "xl" });
  }

  public openPreparacion(pedidoId: number, ventaId: number): void {
    this.router.navigate(["/admin/preparacion/" + pedidoId + "/" + ventaId]);
  }

  public findVentasBig(page: number): void {
    this.formFilters.reset();
    if (this.estado.length == 0 || this.estado == "all") {
      swal.fire("¡Selecciona un estado!", "", "error");
    } else {
      LoaderComponent.status = true;
      this.ventasBigFilter = true;
      this.ventasService.getVentasBig(this.estado, page, this.size).subscribe(
        response => {
          this.ventas = response.content;
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    }
  }

  public changeDate(): void {
    this.formFilters.get("apodo").reset();
  }

  public changeApodo(): void {
    this.formFilters.get("fechaInicio").reset();
    this.formFilters.get("fechaFin").reset();
  }

  public findByFiltros(): void {
    this.ventasBigFilter = false;
    if (this.formFilters.get("apodo").value != "" && this.formFilters.get("apodo").value) {
      this.getVentasByClienteApodo(1);
    } else if (this.formFilters.get("fechaInicio").value != null && this.formFilters.get("fechaFin").value != null) {
      this.getVentasByFechaInicioAndFechaFin(1);
    }
  }

  public getVentasByFechaInicioAndFechaFin(page: number): void {
    if (new Date(this.formFilters.get("fechaFin").value) < new Date(this.formFilters.get("fechaInicio").value)) {
      swal.fire("¡La fecha de venta fin no puede ser menor a la fecha de inicio!", "", "error");
    } else {
      this.isFiltro = true;
      LoaderComponent.status = true;
      this.ventasService.getVentasByFechaInicioAndFechaFin(this.estado, new Date(this.formFilters.get("fechaInicio").value), new Date(this.formFilters.get("fechaFin").value), page, this.size).subscribe(
        response => {
          this.ventas = response.content;
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    }
  }

  public changePage(page: number): void {
    if (this.formFilters.get("apodo").value) {
      this.getVentasByClienteApodo(page);
    } else if (this.estado.length > 0 && (this.formFilters.get("fechaInicio").invalid && this.formFilters.get("fechaFin").invalid)) {
      this.getVentasByEstado(this.estado, page);
    } else if (this.ventasBigFilter) {
      this.findVentasBig(page);
    } else if (this.formFilters.get("fechaInicio").valid && this.formFilters.get("fechaFin").valid) {
      this.getVentasByFechaInicioAndFechaFin(page);
    }
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public getVentasByClienteApodo(page: number): void {
    this.isFiltro = true;
    this.estado = "all";
    this.ventasBigFilter = false;
    LoaderComponent.status = true;
    this.ventasService.getVentasByClienteApodo(this.formFilters.get("apodo").value, page, this.size).subscribe(
      response => {
        this.ventas = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public getVentasByEstado(estado: string, page: number): void {
    this.isFiltro = estado != "all";
    this.estado = estado;
    this.formFilters.reset();
    this.ventasBigFilter = false;
    LoaderComponent.status = true;
    this.ventasService.getVentasByEstado(estado, page, this.size).subscribe(
      response => {
        this.ventas = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = true;
      }
    );
  }

  public changeEstadoByVenta(ventaId: number, pedidoId: number, estado: string): void {
    swal.fire({
      title: '¿Estas seguro de cambiar el estado de la venta?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        //Si hay una fecha estimada significa que es estado finalizado.
        if (this.fechaEstimada != null) {
          estado = "FINALIZADO";
        }
        this.ventasService.changeEstadoByVenta(ventaId, estado, this.fechaEstimada != null ? new Date(this.fechaEstimada.value) : null).subscribe(
          response => {
            this.ngOnInit();
            if (this.fechaEstimadaMD) {
              this.fechaEstimadaMD.close();
            }
            if (estado == "EN PREPARACION") {
              this.openPreparacion(pedidoId, ventaId);
            }
            swal.fire("¡Estado actualizado!", "", "success");
          },
          error => {
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      }
    });
  }

  public openProductosMD(content: any, productos: any[]): void {
    this.ngModal.open(content, { size: "xl" });
    this.productosDataMD = productos;
  }

  public openDireccionMD(content: any, direccion: any): void {
    this.ngModal.open(content, { size: "lg" });
    this.direccionDataMD = direccion;
  }

  public openClienteMD(content: any, cliente: any): void {
    this.ngModal.open(content, { size: "lg" });
    this.clienteDataMD = cliente;
  }

}
