import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { VentasService } from 'src/app/services/ventas.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-preparacion',
  templateUrl: './preparacion.component.html',
  styleUrls: ['./preparacion.component.scss']
})
export class PreparacionComponent implements OnInit {

  public productos: any[];
  public ventaData: any;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public productosListos: any[];
  private ventaId: number;

  constructor(private activateRouter: ActivatedRoute, private catalogoService: CatalogoService, private router: Router, private ventaService: VentasService) { }

  ngOnInit(): void {
    this.productosListos = [];
    this.activateRouter.params.subscribe(
      param => {
        if (param["pedidoId"]) {
          this.getProductosByPedidoId(param["pedidoId"]);
        }
        if (param["ventaId"]) {
          this.ventaId = param["ventaId"];
          this.getVentaByVentaId(this.ventaId);
        }
      }
    );
  }

  public finalizar(): void {
    swal.fire({
      title: '¿Estas seguro de finalizar la preparación?',
      text: "Al finalizar la preparación el pedido cambiara a estado: EN ESPERA DE ENVIO",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        this.ventaService.changeEstadoByVenta(this.ventaId, "EN ESPERA DE ENVIO", null).subscribe(
          response => {
            swal.fire("¡Estado actualizado!", "", "success");
            this.router.navigate(["/admin/ventas"]);
            LoaderComponent.status = false;
          },
          error => {
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      }
    }
    );
  }

  public addProductos(producto: any): void {
    for (let i: number = 0; i < this.productosListos.length; i++) {
      let productoData = this.productosListos[i];
      if (producto.id == productoData.id) {
        this.productosListos.splice(i, 1);
      }
    }
    this.productos.push(producto);
  }

  public addProductosListos(producto: any): void {
    for (let i: number = 0; i < this.productos.length; i++) {
      let productoData = this.productos[i];
      if (producto.id == productoData.id) {
        this.productos.splice(i, 1);
      }
    }
    this.productosListos.push(producto);
  }

  public getVentaByVentaId(ventaId: number): void {
    LoaderComponent.status = true;
    this.ventaService.geVentaByVentaId(ventaId).subscribe(
      response => {
        this.ventaData = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public getProductosByPedidoId(pedidoId: number): void {
    LoaderComponent.status = true;
    this.catalogoService.getProductosByPedidoId(pedidoId).subscribe(
      response => {
        this.productos = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

}
