import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-existencias',
  templateUrl: './existencias.component.html',
  styleUrls: ['./existencias.component.scss']
})
export class ExistenciasComponent implements OnInit {

  @Input()
  public productSelect: any;
  @Output()
  public saveEvent = new EventEmitter<boolean>();
  public existenciaForm: FormGroup;
  public existenciasTypes: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  private payloadExistenciaType: {
    id: number,
    existenciaTypeId: number,
    color: string,
    cantidad: number,
    productoId: number;
  };
  public formExistenciaTitle: string;
  public existenciaSelect: any;
  private refMD: NgbModalRef;

  constructor(private ngbModal: NgbModal, private catalogoService: CatalogoService, private router: Router) { }

  ngOnInit(): void {
    this.refMD = null;
    this.existenciaSelect = null;
    this.formExistenciaTitle = "";
    this.payloadExistenciaType = {
      id: 0,
      existenciaTypeId: 0,
      color: "",
      cantidad: 0,
      productoId: 0
    };
    this.existenciasTypes = [];
    this.existenciaForm = null;
    this.getExistenciasTypes();
  }

  public getExistenciasTypes(): void {
    this.catalogoService.findAllExistenciasTypes().subscribe(
      response => {
        this.existenciasTypes = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private preparePayloadExistencia(): void {
    this.payloadExistenciaType.id = 0;
    if (this.existenciaSelect) {
      this.payloadExistenciaType.id = this.existenciaSelect.id;
    }
    this.payloadExistenciaType.productoId = this.productSelect.id;
    this.payloadExistenciaType.cantidad = Number(this.existenciaForm.get("size").value);
    this.payloadExistenciaType.color = this.existenciaForm.get("color").value;
    this.payloadExistenciaType.existenciaTypeId = Number(this.existenciaForm.get("existenciaId").value);
  }

  public openEditExistencia(content: any, existenciaSelect: any): void {
    this.formExistenciaTitle = "Actualizar existencia: " + existenciaSelect.nombre;
    this.existenciaSelect = existenciaSelect;
    this.existenciaForm = new FormGroup({
      "existenciaId": new FormControl(existenciaSelect.idExistenciaType, Validators.required),
      "size": new FormControl(existenciaSelect.cantidad, [Validators.required, Validators.min(1), Validators.max(9999999)]),
      "color": new FormControl(existenciaSelect.color, Validators.required)
    });
    this.refMD = this.ngbModal.open(content, { size: "xl" });
  }

  public clickSave(): void {
    if (this.existenciaSelect) {
      this.updateExistencia();
    } else {
      this.saveExistencia();
    }
  }

  private isDataModify(): boolean {
    return this.existenciaSelect.color != this.existenciaForm.get("color").value ||
      this.existenciaSelect.idExistenciaType != this.existenciaForm.get("existenciaId").value ||
      this.existenciaSelect.cantidad != this.existenciaForm.get("size").value;
  }

  private updateExistencia(): void {
    if (!this.isDataModify()) {
      swal.fire("¡Por favor modifica algún dato!", "", "info");
      return;
    }
    swal.fire({
      title: '¿Estas seguro de actualizar la información?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        this.preparePayloadExistencia();
        this.catalogoService.updateExistencia(this.payloadExistenciaType).subscribe(
          response => {
            swal.fire("¡Existencia actualizada!", "", "success");
            this.refMD.close();
            this.ngOnInit();
            this.saveEvent.emit(true);
            LoaderComponent.status = false;
          },
          error => {
            this.mensajes.mensajesError(error);
          }
        );
      }
    }
    );
  }

  private saveExistencia(): void {
    LoaderComponent.status = true;
    this.preparePayloadExistencia();
    this.catalogoService.createExistencia(this.payloadExistenciaType).subscribe(
      response => {
        swal.fire("¡Existencia guardada!", "", "success");
        this.refMD.close();
        this.ngOnInit();
        this.saveEvent.emit(true);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openCreateExistencia(content: any): void {
    this.existenciaForm = new FormGroup(
      {
        "existenciaId": new FormControl("", Validators.required),
        "size": new FormControl(0, [Validators.required, Validators.min(1), Validators.max(9999999)]),
        "color": new FormControl(null, Validators.required)
      }
    );
    this.formExistenciaTitle = "Crear existencia";
    this.refMD = this.ngbModal.open(content, { size: "xl" });
  }

}
