import { HttpClient } from '@angular/common/http';
import { stringify } from '@angular/compiler/src/util';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class TallasService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public getTallasByNameOrAll(name: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/existencias/getByName/" + name + "/" + page + "/" + size, {headers : this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createTalla(name: string): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/existencias/create/" + name, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateTalla(name: string, tallaId: number): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/existencias/update?name=" + name + "&tallaId=" + tallaId, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
