import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { swalProviderToken } from '@sweetalert2/ngx-sweetalert2/lib/di';
import { CotizacionService } from 'src/app/services/cotizacion.service';
import { NotificationsComponent } from 'src/app/views/layout/navbar/notifications/notifications.component';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-cotizaciones',
  templateUrl: './cotizaciones.component.html',
  styleUrls: ['./cotizaciones.component.scss']
})
export class CotizacionesComponent implements OnInit {

  public cotizaciones: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public userDataSelect: any;
  public productsSelect: any[];
  public states: any[];
  public historyStates: any[];
  public paginador: any;
  private size: number;
  public filter: string;
  public totalOrdersGroupStates: any[];
  public stateSelect: string;
  public formFilters: FormGroup;

  constructor(private cotizacionService: CotizacionService, private router: Router, private ngbModal: NgbModal) { }

  ngOnInit(): void {
    this.formFilters = new FormGroup({
      "userName": new FormControl("", [Validators.maxLength(100), Validators.required]),
      "dateStart": new FormControl(null, Validators.required),
      "dateEnd": new FormControl(null, Validators.required)
    });
    this.size = 10;
    this.historyStates = [];
    this.productsSelect = [];
    this.userDataSelect = {};
    this.cotizaciones = [];
    this.states = [];
    this.stateSelect = "";
    this.totalOrdersGroupStates = [];
    this.totalOrderGroupByState();
    this.getAllCotizaciones(1, this.size);
    this.getStates();
  }

  public changeDate(): void {
    this.formFilters.get("userName").reset();
  }

  public search(): void {
    if(this.formFilters.get("userName").value) {
      this.findByUserName(1);
    } else if (this.formFilters.get("dateStart").value && this.formFilters.get("dateEnd").value) {
      this.findByDate(1);
    }
  }

  public changeUserName(): void{
    this.formFilters.get("dateStart").reset();
    this.formFilters.get("dateEnd").reset();
  }

  public findByDate(page: number): void {
    let dateEnd: Date = new Date(this.formFilters.get("dateEnd").value);
    let dateStart: Date = new Date(this.formFilters.get("dateStart").value);
    if (dateEnd < dateStart) {
      Swal.fire("¡La fecha de pedido fin no puede ser menor a la fecha de inicio!", "", "error");
    } else {
      LoaderComponent.status = true;
      this.filter = "date";
      this.cotizacionService.findByDate(dateStart, dateEnd, page, this.size).subscribe(
        response => {
          this.cotizaciones = response.content;
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajes.mensajesError(error);
        }
      );
    }
  }

  public findByUserName(page: number): void {
    LoaderComponent.status = true;
    if (this.formFilters.get("userName").value === "" || this.formFilters.get("userName").value === null || this.formFilters.get("userName").value === undefined) {
      this.getAllCotizaciones(1, this.size);
    } else {
      this.filter = "userName";
      this.cotizacionService.findByUserName(this.formFilters.get("userName").value, page, this.size).subscribe(
        response => {
          this.cotizaciones = response.content;
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajes.mensajesError(error);
        }
      );
    }
  }

  public findByState(state: string, page: number): void {
    LoaderComponent.status = true;
    this.filter = "findByState";
    this.stateSelect = state;
    this.cotizacionService.findByState(this.stateSelect, page, this.size).subscribe(
      response => {
        this.cotizaciones = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private totalOrderGroupByState(): void {
    LoaderComponent.status = true;
    this.cotizacionService.totalOrderGroupByState().subscribe(
      response => {
        this.totalOrdersGroupStates = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public changePage(page: number): void {
    if (this.filter === "all") {
      this.getAllCotizaciones(page, this.size);
    } else if (this.filter === "findByState") {
      this.findByState(this.stateSelect, page);
    } else if (this.filter === "userName") {
      this.findByUserName(page);
    } else if (this.filter === "date") {
      this.findByDate(page);
    }
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public changeStateCotizacion(state: string, cotizacionId: number) {
    LoaderComponent.status = true;
    this.cotizacionService.changeState(state, cotizacionId).subscribe(
      response => {
        Swal.fire("¡Estado actualizado!", "", "success");
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private getStates(): void {
    LoaderComponent.status = true;
    this.cotizacionService.getAllStates().subscribe(
      response => {
        this.states = response;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openTimeLineState(content: any, cotizacionId: number): void {
    LoaderComponent.status = true;
    this.cotizacionService.findAllHistoryStates(cotizacionId).subscribe(
      response => {
        this.historyStates = response;
        this.ngbModal.open(content, { size: "xl" });
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openProductsMD(content: any, products: any[]): void {
    this.productsSelect = products;
    this.ngbModal.open(content, { size: "xl" });
  }

  public openUserDataMD(content: any, userData: any): void {
    this.userDataSelect = userData;
    this.ngbModal.open(content, { size: 'lg' });
  }

  private getAllCotizaciones(page: number, size: number): void {
    LoaderComponent.status = true;
    this.filter = "all";
    this.cotizacionService.getAll(page, size).subscribe(
      response => {
        this.cotizaciones = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

}
