import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from '../services/utilidades';

@Injectable({
  providedIn: 'root'
})
export class ArchivosService {

  private headers: HttpHeaders;
  private utilidades: Utilidades = new Utilidades();

  constructor(private http:HttpClient) { }

  public cargarArchivo(archivo: FormData): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/archivo/cargar", archivo, {headers: this.utilidades.getHeadersArchivo()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }
  
  public downloadTemplateExcelProductos(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/archivo/download/template/excel/productos", {responseType: "blob", headers: this.utilidades.getHeadersDownloadFiles()});
  }

  public uploadPlantillaProductosExcel(archivo: FormData): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/archivo/upload/template/excel", archivo, {headers: this.utilidades.getHeadersArchivo()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
