import { Component, OnInit } from '@angular/core';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import swal from 'sweetalert2';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { ArchivosService } from 'src/app/services/archivos.service';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-inventario',
  templateUrl: './inventario.component.html',
  styleUrls: ['./inventario.component.scss']
})
export class InventarioComponent implements OnInit {

  public products: any [];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public imageSelect: string;
  public productSelect: any;
  private mdRef: NgbModalRef;
  public paginador: any;
  public productName: FormControl;
  public productsStates: any[];
  public productsTypes: any[];
  public typeSelect: FormControl;
  public filters: string[];

  constructor(private catalogoService: CatalogoService, private router: Router, private ngbmodal: NgbModal, private archivosService: ArchivosService) {
  }

  ngOnInit(): void {
    this.filters = [];
    this.productName = new FormControl("", [Validators.required, Validators.maxLength(45)]);
    this.typeSelect = new FormControl("");
    this.mdRef = null;
    this.productSelect = null;
    this.products = [];
    this.imageSelect = "";
    this.productsStates = [];
    this.productsTypes = [];
    this.getProductTypes();
    this.getProductos(1, 10, "all");
    this.getProductsStates();
  }

  public getProductsByExistenciasInZero(page: number): void {
    LoaderComponent.status = true;
    this.filters = [];
    this.filters.push("BY_EXISTENCIAS_ZERO");
    this.catalogoService.getProductsByExistenciasInZero(page, 10).subscribe(
      response => {
        this.products = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public getProductsByState(state: boolean, page: number): void {
    LoaderComponent.status = true;
    this.productName.reset();
    this.typeSelect.reset();
    this.filters = [];
    this.filters.push("BY_STATE_" + state.valueOf());
    this.catalogoService.getProductsByState(state, page, 10).subscribe(
      response => {
        this.products = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public resetFiltros(): void {
    this.filters = [];
    this.typeSelect.reset();
    this.productName.reset();
    this.getProductos(1, 10, "all");
  }

  public getProductsByType(page: number): void {
    LoaderComponent.status = true;
    this.productName.reset();
    this.filters = [];
    this.filters.push("BY_TYPE");
    this.catalogoService.getProductsByType(Number.parseInt(this.typeSelect.value), page, 10).subscribe(
      response => {
        this.products = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private getProductTypes(): void {
    LoaderComponent.status = true;
    this.catalogoService.findCategorias().subscribe(
      response => {
        this.productsTypes = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private getProductsStates(): void {
    LoaderComponent.status = true;
    this.catalogoService.getStatesProducts().subscribe(
      response => {
        this.productsStates = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public findByName(page: number): void {
    this.filters = [];
    this.filters.push("BY_NAME");
    this.typeSelect.reset();
    if(this.productName.value == "") {
      this.getProductos(1, 10, "all");
    } else {
      this.getProductos(page, 10, this.productName.value);
    }
  }

  public changePage(page: number): void {
    if(this.filters.length > 0) {
      if(this.filters[0] == "BY_NAME") {
        this.getProductos(page, 10, this.productName.value);
      } else if (this.filters[0] == "BY_TYPE") {
        this.getProductsByType(page);
      } else if (this.filters[0] == "BY_STATE_true") {
        this.getProductsByState(true, page);
      } else if (this.filters[0] == "BY_STATE_false") {
        this.getProductsByState(false, page);
      } else if (this.filters[0] == "BY_EXISTENCIAS_ZERO") {
        this.getProductsByExistenciasInZero(page);
      }
    } else {
      this.getProductos(page, 10, "all");
    }
  }

  public productForm(productId: number): void {
    if(productId != null) {
      this.router.navigate(["/admin/productForm"], {queryParams : {"productId": productId}});
    } else {
      this.router.navigate(["/admin/productForm"]);
    }
  }

  public saveExistenciaEvent(value: Boolean): void {
    if(value) {
      this.mdRef.close();
      this.ngOnInit();
    }
  }

  public changeStateByProduct(state: boolean, productId: number): void {
    LoaderComponent.status = true;
    this.catalogoService.changeStateProduct(productId, state).subscribe(
      response => {
        this.ngOnInit();
        if (state) {
          swal.fire("¡Producto activado!", "", "success");
        } else {
          swal.fire("¡Producto desactivado!", "", "success");
        }
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openExistencias(content: any, productSelect: any): void {
    LoaderComponent.status = true;
    this.productSelect = productSelect;
    this.mdRef = this.ngbmodal.open(content, { size: "xl" });
  }

  public openImage(content: any, imageSelect: string): void {
    this.imageSelect = imageSelect;
    this.ngbmodal.open(content, { size: "xl" });
  }

  public getProductos(page: number, size: number, name: string): void {
    LoaderComponent.status = true;
    this.catalogoService.findAllProductosNotState(name, page, size).subscribe(
      response => {
        this.products = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

}
