import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ArchivosService } from 'src/app/services/archivos.service';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-product-form',
  templateUrl: './product-form.component.html',
  styleUrls: ['./product-form.component.scss']
})
export class ProductFormComponent implements OnInit {

  public title: string;
  public isCreate: boolean;
  public productForm: FormGroup;
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public productSelect: any;
  private imageResource: File;
  private payloadProductUpdate: {
    id: number,
    nombre: string,
    precio: number,
    precioCompra: number,
    precioDistribuidor: number,
    descripcion: string,
    tipoId: number,
    imagenId: number,
    existencias: any[]
  };
  public types: any;
  public existencias: any[];

  constructor(private activatedRouter: ActivatedRoute, private router: Router, private archivoService: ArchivosService, private catalogoService: CatalogoService, private nbgModal: NgbModal) { }

  ngOnInit(): void {
    this.existencias = [];
    this.payloadProductUpdate = {
      id: 0,
      nombre: "",
      precio: 0,
      precioCompra: 0,
      precioDistribuidor: 0,
      descripcion: "",
      tipoId: 0,
      imagenId: 0,
      existencias: []
    };
    this.imageResource = null;
    this.productForm = null;
    this.productSelect = null;
    this.productForm = new FormGroup({
      "name": new FormControl("", [Validators.required, Validators.maxLength(20)]),
      "value": new FormControl("", [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
      "valueCompra": new FormControl("", [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
      "valueDistribuidor": new FormControl("", [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
      "type": new FormControl("", [Validators.required]),
      "description": new FormControl("", [Validators.required, Validators.maxLength(200)]),
      "image": new FormControl("", [Validators.required])
    });
    this.isCreate = true;
    this.activatedRouter.queryParams.subscribe(
      param => {
        if (param["productId"]) {
          this.title = "Modificar producto";
          this.isCreate = false;
          LoaderComponent.status = true;
          if (window.isNaN(param["productId"])) {
            this.router.navigate(["/admin/inventario"]);
          } else {
            this.getProductSelect(Number(param["productId"]));
          }
        } else {
          this.title = "Crear producto";
          this.getTypes();
        }
      }
    );
  }

  public saveExistenciaEvent(existencias: any[]): void {
    this.existencias = existencias;
    swal.fire("¡Existencia guardada!", "", "success");
  }

  public openExistencias(content: any): void {
    this.nbgModal.open(content,{size: "xl"});
  }

  public getTypes(): void {
    LoaderComponent.status = true;
    this.catalogoService.findCategorias().subscribe(
      response => {
        this.types = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public getProductSelect(productSelectId: number): void {
    this.catalogoService.getProductNotStateById(productSelectId).subscribe(
      response => {
        this.productSelect = response;
        this.catalogoService.findCategorias().subscribe(
          response => {
            this.types = response;
            this.productForm = new FormGroup({
              "name": new FormControl(this.productSelect.nombre, [Validators.required, Validators.maxLength(20)]),
              "value": new FormControl(new Intl.NumberFormat("es-CO").format(this.productSelect.precio), [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
              "valueCompra": new FormControl(new Intl.NumberFormat("es-CO").format(this.productSelect.precioCompra), [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
              "valueDistribuidor": new FormControl(new Intl.NumberFormat("es-CO").format(this.productSelect.precioDistribuidor), [Validators.required, Validators.pattern("[0-9,.]*"), Validators.maxLength(19)]),
              "type": new FormControl(Number(this.productSelect.tipoId), [Validators.required]),
              "description": new FormControl(this.productSelect.descripcion, [Validators.required, Validators.maxLength(200)]),
              "image": new FormControl("")
            });
            LoaderComponent.status = false;
          },
          error => {
            this.mensajes.mensajesError(error);
          }
        );
      },
      error => {
        this.router.navigate(["/admin/inventario"]);
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openFileBrowser(event: any): void {
    event.preventDefault();
    let element: HTMLElement = document.querySelector("#fileUploadInputExample") as HTMLElement;
    element.click()
  }

  public handleFileInput(event: any): void {
    if (event.target.files.length) {
      let element: HTMLElement = document.querySelector("#fileUploadInputExample + .input-group .file-upload-info") as HTMLElement;
      let fileName = event.target.files[0].name;
      element.setAttribute('value', fileName)
      this.imageResource = event.target.files[0];
    }
  }

  public changeValue(): void {
    if (this.productForm.get("value").valid) {
      this.productForm.get("value").setValue(Intl.NumberFormat("es-CO").format(this.convertNumberMoney(String(this.productForm.get("value").value))));
    }
    if(this.productForm.get("valueCompra").valid) {
      this.productForm.get("valueCompra").setValue(Intl.NumberFormat("es-CO").format(this.convertNumberMoney(String(this.productForm.get("valueCompra").value))));
    }
    if(this.productForm.get("valueDistribuidor").valid) {
      this.productForm.get("valueDistribuidor").setValue(Intl.NumberFormat("es-CO").format(this.convertNumberMoney(String(this.productForm.get("valueDistribuidor").value))));
    }
  }

  private updateProductService(): void {
    this.catalogoService.updateProduct(this.payloadProductUpdate).subscribe(
      response => {
        swal.fire("¡Información guardada!", "", "success");
        this.router.navigate(["/admin/inventario"]);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  private createPayloadExistencias(): any[] {
    let existenciasPayload: any [] = [];
    for(let i: number = 0; i < this.existencias.length; i ++) {
      let existencia: any = this.existencias[i];
      existenciasPayload.push({
        talla: Number.parseInt(existencia.existenciaId),
        color: existencia.color,
        cantidad: existencia.size
      });
    }
    return existenciasPayload;
  }

  private prepareRequest(): void {
    if(!this.isCreate) {
      this.payloadProductUpdate.id = Number(this.productSelect.id);
      this.payloadProductUpdate.imagenId = this.productSelect.imagenId;
    } else {
      this.payloadProductUpdate.existencias = this.createPayloadExistencias();
    }
    this.payloadProductUpdate.nombre = this.productForm.get("name").value;
    this.payloadProductUpdate.precio = this.convertNumberMoney(String(this.productForm.get("value").value));
    this.payloadProductUpdate.precioCompra = this.convertNumberMoney(String(this.productForm.get("valueCompra").value));
    this.payloadProductUpdate.precioDistribuidor = this.convertNumberMoney(String(this.productForm.get("valueDistribuidor").value));
    this.payloadProductUpdate.tipoId = Number(this.productForm.get("type").value);
    this.payloadProductUpdate.descripcion = this.productForm.get("description").value;
  }

  private convertNumberMoney(value: string): number{
    let valueParce: string = value.split(".").join("");
    valueParce = valueParce.split(",").join("");
    return Number(valueParce);
  }

  private updateProduct(): void {
    if (!this.isChangeDataProduct()) {
      swal.fire("¡Por favor modifica la información!", "", "warning");
      return;
    }
    if (this.productSelect && this.productForm.valid) {
      swal.fire({
        title: '¿Estas seguro de guardar esta información?',
        text: "",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Si!',
        cancelButtonText: 'No!'
      }).then((result) => {
        if (result.isConfirmed) {
          LoaderComponent.status = true;
          this.prepareRequest();
          if (this.imageResource != null) {
            let image: FormData = new FormData();
            image.append("archivo", this.imageResource);
            this.archivoService.cargarArchivo(image).subscribe(
              response => {
                this.payloadProductUpdate.imagenId = response.id;
                this.updateProductService();
              },
              error => {
                this.mensajes.mensajesError(error);
              }
            );
          } else {
            this.updateProductService();
          }
        }
      })
    } else {
      swal.fire("¡Por favor intentalo de nuevo!", "", "error");
    }
  }

  public createProduct(): void {
    LoaderComponent.status = true;
    this.prepareRequest();
    let image: FormData = new FormData();
    image.append("archivo", this.imageResource);
    this.archivoService.cargarArchivo(image).subscribe(
      response => {
        this.payloadProductUpdate.imagenId = response.id;
        this.catalogoService.createProduct(this.payloadProductUpdate).subscribe(
          response2 => {
            swal.fire("¡Producto creado!", "", "success");
            this.router.navigate(["/admin/inventario"]);
            LoaderComponent.status = false;
          },
          error => {
            this.mensajes.mensajesError(error);
          }
        );
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public clickSaveProducto(): void {
    if (this.isCreate) {
      this.createProduct();
    } else {
      this.updateProduct();
    }
  }

  private isChangeDataProduct(): boolean {
    let valueParce = this.convertNumberMoney(String(this.productForm.get("value").value));
    let valueCompraParce = this.convertNumberMoney(String(this.productForm.get("valueCompra").value));
    let valueDistribuidorParce = this.convertNumberMoney(String(this.productForm.get("valueDistribuidor").value));
    return this.productForm.get("name").value != this.productSelect.nombre ||
      valueParce != this.productSelect.precio ||
      valueCompraParce != this.productSelect.precioCompra ||
      valueDistribuidorParce != this.productSelect.precioDistribuidor ||
      Number(this.productForm.get("type").value) != this.productSelect.tipoId ||
      this.productForm.get("description").value != this.productSelect.descripcion ||
      this.imageResource != null;
  }

}
