import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { filter } from 'rxjs/operators';
import { UsuarioService } from 'src/app/services/usuario.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {

  public users: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public name: FormControl;
  public userSelect: any;
  private refMD: NgbModalRef;
  public totalData: any[];
  public filterSelect: string;
  private filterType: string;
  public paginador: any;
  private filterUse: string;

  constructor(private usuarioService: UsuarioService, private router: Router, private ngbModal: NgbModal) { }

  ngOnInit(): void {
    this.filterType = "";
    this.filterSelect = "";
    this.userSelect = null;
    this.name = new FormControl("", Validators.maxLength(20));
    this.users = [];
    this.getTotalData();
    this.findUsersByNameOrAll("all", 1);
  }

  public changePage(page: number): void {
    if(this.filterUse != "byName") {
      this.name.reset();
    }
    if(this.filterUse != "byRol" && this.filterUse != "byState") {
      this.filterType = "";
      this.filterSelect = "";
    }
    if(this.filterUse == "all") {
      this.findUsersByNameOrAll("all", page);
    } else if (this.filterUse == "byName") {
      this.findUsersByNameOrAll(this.name.value, page);
    } else if (this.filterUse == "byRol" || this.filterUse == "byState") {
      this.getAllUsersByFilterType(page);
    }
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public findByFilter(filterName: string, filterType: string): void {
    this.filterSelect = filterName;
    this.filterType = filterType;
    this.getAllUsersByFilterType(1);
  }

  private getAllUsersByFilterType(page: number): void {
    if(this.filterType == "STATE") {
      LoaderComponent.status = true;
      this.usuarioService.getAllUsersByState(this.filterSelect == "true" , page, 10).subscribe(
        response => {
          this.users = response.content;
          this.paginadorInit(response);
          this.filterUse = "byState";
          LoaderComponent.status = false;
        },
        error => {
          this.mensajes.mensajesError(error);
        }
      );
    } else if (this.filterType == "ROL") {
      LoaderComponent.status = true;
      this.usuarioService.getAllUsersByRol(this.filterSelect, page, 10).subscribe(
        response => {
          this.users = response.content;
          this.paginadorInit(response);
          this.filterUse = "byRol";
          LoaderComponent.status = false;
        },
        error => {
          this.mensajes.mensajesError(error);
        }
      );
    }
  }

  private getTotalData(): void {
    LoaderComponent.status = true;
    this.usuarioService.getTotalData().subscribe(
      response => {
        this.totalData = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public changeStateUser(userId: number, state: boolean): void {
    LoaderComponent.status = true;
    this.usuarioService.changeStateUser(userId, state).subscribe(
      response => {
        swal.fire(state ? "¡Activado!" : "¡Desactivado!", "", "success");
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public eventUserForm(message: string): void {
    swal.fire(message, "", "success");
    LoaderComponent.status = false;
    this.refMD.close();
    this.ngOnInit();
  }

  public openFormUser(content: any, userSelect: any): void {
    this.userSelect = userSelect;
    LoaderComponent.status = true;
    this.refMD = this.ngbModal.open(content, {size: "xl"});
  }

  public changeName(): void{
    if(this.name.valid) {
      if(this.name.value == "") {
        this.findUsersByNameOrAll("all", 1);
      } else {
        this.findUsersByNameOrAll(this.name.value, 1);
      }
    }
  }

  private findUsersByNameOrAll(name: string, page: number): void {
    LoaderComponent.status = true;
    this.usuarioService.findUsersByNameOrAll(name, page, 10).subscribe(
      response => {
        this.users = response.content;
        this.paginadorInit(response);
        this.filterUse = name != "all" ? "byName" : "all";
        LoaderComponent.status = false;
      },
      error =>{
        this.mensajes.mensajesError(error);
      }
    );
  }

}
