import { getLocaleFirstDayOfWeek } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-existencias-create',
  templateUrl: './existencias-create.component.html',
  styleUrls: ['./existencias-create.component.scss']
})
export class ExistenciasCreateComponent implements OnInit {

  public existenciaForm: FormGroup;
  public formExistenciaTitle: string;
  private refMD: NgbModalRef;
  @Input()
  public existencias: any[];
  @Output()
  public saveEvent: EventEmitter<any[]> = new EventEmitter<any[]>();
  public existenciasTypes: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  private isUpdate: boolean;
  private existenciaSelect: any;

  constructor(private ngbModal: NgbModal, private catalogoService: CatalogoService, private router: Router) { }

  ngOnInit(): void {
    this.isUpdate = false;
    this.catalogoService.findAllExistenciasTypes().subscribe(
      response => {
        this.existenciasTypes = response;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public deleteExistencia(existenciaSelect: any): void {
    swal.fire({
      title: '¿Estas seguro de eliminar esta existencia?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        for(let i: number = 0; i < this.existencias.length; i ++) {
          let existencia: any = this.existencias[i];
          if(existencia.existenciaId == existenciaSelect.existenciaId && existencia.color == existenciaSelect.color) {
            this.existencias.splice(i, 1);
            swal.fire("¡Existencia eliminada!", "", "success");
            break;
          }
        }
      }
    });
  }

  public openCreateExistencia(content: any): void {
    this.isUpdate = false;
    this.existenciaForm = new FormGroup(
      {
        "existenciaId": new FormControl("", Validators.required),
        "size": new FormControl(0, [Validators.required, Validators.min(1), Validators.max(9999999)]),
        "color": new FormControl(null, Validators.required)
      }
    );
    this.formExistenciaTitle = "Crear existencia";
    this.refMD = this.ngbModal.open(content, { size: "xl" });
  }

  public openUpdateExistencia(content: any, existencia: any) {
    this.isUpdate = true;
    this.existenciaSelect = existencia;
    this.existenciaForm = new FormGroup(
      {
        "existenciaId": new FormControl(existencia.existenciaId + "-" + existencia.existenciaName, Validators.required),
        "size": new FormControl(existencia.size, [Validators.required, Validators.min(1), Validators.max(9999999)]),
        "color": new FormControl(existencia.color, Validators.required)
      }
    );
    this.formExistenciaTitle = "Actualizar existencia: " + existencia.existenciaName;
    this.refMD = this.ngbModal.open(content, { size: "xl" });
  }

  private isExistByExistenciaIdAndColor(existenciaConvert: string): boolean {
    let existenciaResult: any = this.existencias.filter(existencia => {
      let existenciaConvert: string = this.existenciaForm.get("existenciaId").value;
      return existencia.existenciaId == Number.parseInt(existenciaConvert.split("-")[0]) && existencia.color == this.existenciaForm.get("color").value;
    });
    return existenciaResult.length > 0;
  }

  public saveExistencia(): void {
    if (this.isUpdate) {
      let existenciaResult: any = this.existencias.filter(existencia => {
        return existencia.existenciaId == this.existenciaSelect.existenciaId && existencia.color == this.existenciaSelect.color;
      });
      if(existenciaResult.length == 0) {
        swal.fire("¡La existencia no fue encontrada!", "Comunicate con el equipo de soporte, para solucionar el problema, gracias.", "error");
        return;
      }
      if (this.isExistByExistenciaIdAndColor(this.existenciaForm.get("existenciaId").value)) {
        swal.fire("¡Ya existe esta existencia!", "", "error");
        return;
      }
      existenciaResult[0].color = this.existenciaForm.get("color").value;
      existenciaResult[0].size = this.existenciaForm.get("size").value;
      let existencia: string = this.existenciaForm.get("existenciaId").value;
      existenciaResult[0].existenciaId =  Number.parseInt(existencia.split("-")[0]);
      existenciaResult[0].existenciaName = existencia.split("-")[1];
    } else {      
      if (this.isExistByExistenciaIdAndColor(this.existenciaForm.get("existenciaId").value)) {
        swal.fire("¡Ya existe esta existencia!", "", "error");
        return;
      }
      let existencia: string = this.existenciaForm.get("existenciaId").value;
      this.existencias.push({
        color: this.existenciaForm.get("color").value,
        size: this.existenciaForm.get("size").value,
        existenciaId: existencia.split("-")[0],
        existenciaName: existencia.split("-")[1]
      });
    }
    this.existenciaForm.reset();
    this.saveEvent.emit(this.existencias);
    this.refMD.close();
  }

}
