import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { TallasService } from '../../../../../../services/tallas.service';
import swal from 'sweetalert2';

@Component({
  selector: 'app-talla-config',
  templateUrl: './talla-config.component.html',
  styleUrls: ['./talla-config.component.scss']
})
export class TallaConfigComponent implements OnInit {

  public title: string;
  public name: FormControl;
  public tallas: any[];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  public paginador: any;
  public mdRef: NgbModalRef;
  private isCreate: boolean;
  public existenciaSelect: any;

  constructor(private ngbModal: NgbModal, private tallasService: TallasService, private router: Router) { }

  ngOnInit(): void {
    this.existenciaSelect = null;
    this.isCreate = false;
    this.tallas = [];
    this.title = "";
    this.name = new FormControl("", [Validators.required, Validators.maxLength(20)]);
    this.getTallasByNameOrAll("all", 1);
  }

  public clickSave(): void {
    if(this.isCreate) {
      this.createTalla();
    } else {
      this.updateTalla();
    }
  }

  public updateTalla(): void {
    LoaderComponent.status = true;
    this.tallasService.updateTalla(this.name.value, this.existenciaSelect.id).subscribe(
      response =>{ 
        swal.fire("¡Talla actualizada!", "", "success");
        this.mdRef.close();
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public createTalla(): void {
    LoaderComponent.status = true;
    this.tallasService.createTalla(this.name.value).subscribe(
      response => {
        swal.fire("¡Talla creada!", "", "success");
        this.mdRef.close();
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public changeName(): void {
    if(this.name.value == "") {
      this.getTallasByNameOrAll("all", 1);
    } else {
      this.getTallasByNameOrAll(this.name.value, 1);
    }
  }

  public changePage(page: number): void {
    if(this.name.value != "") {
      this.getTallasByNameOrAll(this.name.value, page);
    } else {
      this.getTallasByNameOrAll("all", page);
    }
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public getTallasByNameOrAll(name: string, page: number): void {
    LoaderComponent.status = true;
    this.tallasService.getTallasByNameOrAll(name, page, 10).subscribe(
      response => {
        this.tallas = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
  }

  public openFormMD(content: any, existencia: any): void {
    this.name.reset();
    if (existencia) {
      this.existenciaSelect = existencia;
      this.isCreate = false;
      this.title = "Actualizar talla: " + existencia.name;
      this.name.setValue(existencia.name);
    } else {
      this.isCreate = true;
      this.title = "Crear talla";
    }
    this.mdRef = this.ngbModal.open(content, { size: "lg" });
  }
}
